import { SxProps, Theme } from "@rp/react/web/mui/core"

const getContainerWidth = (isHomePage: boolean, idx: number): number | string => {
  if (isHomePage) return 174
  return idx === 0 ? "503px" : "243px"
}

const getContainerSX = (isHomePage: boolean, idx: number): SxProps<Theme> => ({
  zIndex: 1,
  width: getContainerWidth(isHomePage, idx),
  backgroundColor: "#252525",
  borderRadius: "16px",
  marginX: isHomePage ? null : "8px",
  marginY: "16px",
  padding: "8px 12px 12px 12px",
  boxShadow: "0px 4px 4px 0px #00000040",
  height: "187px",
  display: "flex",
  flexDirection: "column",
  "&:hover": {
    backgroundColor: "#2E2E2E",
    cursor: "pointer",
    boxShadow: "0px 4px 14px 0px #000000A6",
  },
})

export { getContainerSX }
